"use strict";
boann.controller('showroomController', ['$scope', '$http', '$window', function($scope, $http, $window) {

    var URI = controler.view+"showroom/showroom.php";

    getCars();

    function getCars(){
        $http.get(URI, {params:{action:'getCars'}}).then(function(data){
            if(data.status === 200){
                $scope.cars = data.data;
                console.log(data.data);
            }
        });
    }

}]);
